import React, { useEffect, useRef } from "react";
import { gsap } from "gsap";
// Note: MorphSVGPlugin is a premium GSAP plugin
import { MorphSVGPlugin } from "gsap/MorphSVGPlugin";

// Register the MorphSVG plugin
gsap.registerPlugin(MorphSVGPlugin);

interface WaveProps {
  /** Optional additional classNames for the wave container */
  className?: string;
  /** Background color of the content area */
  backgroundColor?: string;
  /** Text color for any content within the wave */
  textColor?: string;
}

/**
 * Wave component that animates an SVG path based on scroll position
 */
const Wave: React.FC<WaveProps> = ({
  className = "",
  backgroundColor = "bg-black",
  textColor = "text-white",
}) => {
  // Reference to the SVG path element
  const pathRef = useRef<SVGPathElement>(null);

  useEffect(() => {
    if (!pathRef.current) return;

    const path = pathRef.current;
    const toPath = path.getAttribute("data-to");

    // Create the animation but pause it initially
    const animation = gsap.to(path, {
      morphSVG: toPath, // Using morphSVG plugin
      duration: 1,
      ease: "power1.out",
      paused: true,
    });

    // Function to update animation based on scroll position
    const handleScroll = () => {
      const scrollY = window.scrollY;
      // Calculate progress based on scroll position (0-200px range)
      const progress = Math.max(Math.min(scrollY, 200), 1) / 200;
      animation.progress(progress);
    };

    // Initial call to set starting position
    handleScroll();

    // Add scroll event listener
    window.addEventListener("scroll", handleScroll);

    // Cleanup function to remove event listener
    return () => {
      window.removeEventListener("scroll", handleScroll);
      animation.kill();
    };
  }, []);

  return (
    <div
      className={`relative mt-24 pt-[15.7%] ${textColor} ${backgroundColor} bg-origin-content bg-no-repeat h-screen ${className}`}
    >
      <svg className="absolute top-0 right-0 left-0 w-full" viewBox="0 0 1000 158">
        <path
          ref={pathRef}
          d="M1000,30.1V158H0V0c169.7,238.5,374.3-76,502.6,106.8C615.7,268.2,759.9-15.4,1000,30.1z"
          data-to="M1000,157v1H0v-1h502.6H1000z"
        />
      </svg>
      {/* Content can be added here as children */}
    </div>
  );
};

export default Wave;
